import { Component, OnInit, Input } from "@angular/core";
import { Router } from "@angular/router";
import { DatabaseService } from "src/app/database.service";

@Component({
  selector: "app-card-domand",
  templateUrl: "./card-domand.component.html",
  styleUrls: ["./card-domand.component.scss"]
})
export class CardDomandComponent implements OnInit {
  @Input() item: any;
  constructor(private router: Router, private data: DatabaseService) {}
  ngOnInit() {}

  toDetail() {
    // this.data.product = this.item;
    localStorage.setItem("productID", this.item.ID);
    this.router.navigate(["/product/detail"]);

    window.location.reload();
  }
}
