import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { DatabaseService } from "src/app/database.service";

@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"]
})
export class LoginComponent implements OnInit {
  account: string;
  password: string;

  constructor(private router: Router, private data: DatabaseService) {}

  ngOnInit() {}

  login() {
    if (this.account === "") {
      alert("請輸入帳號！");
      return;
    }

    const user = this.data.accountList.find(item => {
      return item.account === this.account;
    });

    if (user) {
      console.log(user);
      if (user.pwd !== this.password) {
        alert("帳號或密碼錯誤！");
        return;
      }

      localStorage.setItem("account", this.account);
      localStorage.setItem("name", user.nickname);
      this.data.isLogin = true;
      this.router.navigate(["/home"]);
    } else {
      alert("帳號或密碼錯誤！");
    }
  }
}
