import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { HomeComponent } from "./pages/home/home.component";
import { InformationComponent } from "./pages/user/information/information.component";
import { WarehouseComponent } from "./pages/user/warehouse/warehouse.component";
import { DemandComponent } from "./pages/production/demand/demand.component";
import { ProductComponent } from "./pages/production/product/product.component";
import { LoginComponent } from "./components/login/login.component";
import { ProductDetailComponent } from "./pages/production/product-detail/product-detail.component";

const routes: Routes = [
  { path: "", redirectTo: "/home", pathMatch: "full" },
  { path: "home", component: HomeComponent },
  { path: "product", component: ProductComponent },
  { path: "demand", component: DemandComponent },
  { path: "information", component: InformationComponent },
  { path: "warehouse", component: WarehouseComponent },
  { path: "login", component: LoginComponent },
  { path: "product/detail", component: ProductDetailComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
